<template>
	<!--  -->
	<div class="account-container">
		<form class="account-container-form" v-on:submit.prevent="login">
			<p>
				<span class="phone-ico"></span>
				<input type="text" placeholder="" maxlength="11" class="phone" v-model.lazy.trim="phone" />
			</p>
			<p>
				<span class="pwd-ico"></span>
				<input type="password" placeholder="" class="pwd" v-model.lazy.trim="pwd" />
			</p>
			<p>
				<input type="submit" value="	" class="account-btn" />
			</p>
			<router-link tag="a" class="phone-prompt" :to="{name: 'ForgetPwdFirstStep'}"></router-link>
		</form>
	</div>
</template>

<script type="text/ecmascript-6">
	export default {
		data () {
			return {
				phone: '',
				pwd: ''
			}
		},
		methods: {
			login () {
				if (!(/^((13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8})$/).test(this.phone)) {
					alert('');
					return false;
				}

				if (!(/\w{6,}/).test(this.pwd)) {
					alert('6');
					return false;
				}

				this.$http.post('/login/loginForm', {phone: this.phone, pwd: this.pwd}, {emulateJSON: true}).then((result) => {
					const userMsg = JSON.parse(result.bodyText);
					switch (userMsg.backInfo) {
						case '0':
							alert('');
							break;
						case '1':
							window.sessionStorage.userMsg = JSON.stringify(userMsg);
							this.$router.push({name: 'Personal'})
							break;
						default:
							alert('');
					}
				});
			}
		},
		mounted () {
			if (!window.sessionStorage) {
				alert('');
			}
		}
	}
</script>


