<template>
	<!--  -->
	<div class="account-container">
		<form v-on:submit.prevent="regist" class="account-container-form">
			<p>
				<span class="phone-ico"></span>
				<input type="text" placeholder="" maxlength="11" class="phone" v-model.lazy.trim="phone" />
			</p>
			<p>
				<span class="pwd-ico"></span>
				<input type="password" placeholder="6" class="pwd" v-model.lazy.trim="pwd" />
			</p>
			<p>
				<span class="pwd-ico"></span>
				<input type="password" placeholder="" class="pwd" v-model.lazy.trim="confirmPwd" />
			</p>
			<p>
				<input type="submit" value="	" class="account-btn" />
			</p>
		</form>
	</div>
</template>

<script type="text/ecmascript-6">
	export default {
		data () {
			return {
				phone: '',
				pwd: '',
				confirmPwd: ''
			}
		},
		methods: {
			regist () {
				if (!(/^((13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8})$/).test(this.phone)) {
					alert('');
					return false;
				}

				if (!(/\w{6,}/).test(this.pwd)) {
					alert('6');
					return false;
				}

				if (this.pwd !== this.confirmPwd) {
					alert('');
					return false;
				}

				this.$http.post('/regist/registForm', {phone: this.phone, pwd: this.pwd}, {emulateJSON: true}).then((result) => {
					switch (JSON.parse(result.bodyText).backInfo) {
						case '0':
							alert('');
							break;
						case '1':
							alert('');
							window.location.href = '/#/login';
							break;
						default:
							alert('');
					}
				});
			}
		}
	}
</script>


